import type { PropsWithChildren } from 'react';
import React, { forwardRef, useCallback, useEffect } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';
import classNames from 'classnames';
import Button from '../Button';
import Icon from '../Icon';
import { KeyCode } from '../util';
import { ModalHeading } from './ModalHeading';
import { ModalContent } from './ModalContent';
import { ModalFooter } from './ModalFooter';

// eslint-disable-next-line @typescript-eslint/no-empty-function -- it's a noop
const noop = function () {};

const StyledRoot = styled.div`
  background-color: ${(props) => props.theme.token.color.white};
  box-shadow: ${(props) => props.theme.token.shadow.box.medium};
  top: 20vh;
  position: relative;

  & .arthur-modal-inner {
    position: relative;

    & .arthur-modal-close {
      background-color: transparent;
      border: 0px none;
      position: absolute;
      cursor: pointer;
    }
  }
`;

const StyledOverlay = styled.div`
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  display: flex;
  align-items: baseline;
  justify-content: center;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  z-index: 999;
`;

/**
 *
 */
export interface ModalProps {
  className?: string;
  /**
   * @default false
   */
  disabled?: boolean;
  /**
   * Describes the button text for the cancellation button
   *
   */
  secondaryButtonText?: string;

  /**
   * Describes the button text for the confirmation button
   *
   */
  primaryButtonText?: string;

  /**
   * Describes the header text of this component.
   *
   * @default undefined
   */
  header?: string;

  /**
   * Handles events that indicate that the component should hide itself.
   *
   * @default undefined
   */
  onClose?: () => void;

  /**
   * Handles button events for supported variants.
   *
   * @default undefined
   */
  onButtonClick?: (event: React.MouseEvent<HTMLButtonElement>, type: 'primary' | 'secondary') => void;
  /**
   * Describes whether this component should be hidden.
   *
   * @default false
   */
  hidden?: boolean;
}

const Modal = forwardRef<HTMLDivElement, PropsWithChildren<ModalProps>>((props, forwardedRef) => {
  const {
    children,
    className,
    disabled,
    hidden = true,
    onClose = noop,
    onButtonClick,
    header,
    primaryButtonText,
    secondaryButtonText,
    ...rest
  } = props;

  useEffect(() => {
    function onEscape(event: KeyboardEvent) {
      if (event.code === (KeyCode.Escape as string) && disabled !== true) {
        onClose();
      }
    }

    document.addEventListener('keydown', onEscape);
    return () => {
      document.removeEventListener('keydown', onEscape);
    };
  }, []);

  const classes = classNames(className, 'arthur-modal', {
    'arthur-modal--disabled': disabled === true,
  });

  const onOverlayClick = useCallback(
    (event: React.MouseEvent<HTMLDivElement>) => {
      if ((event.target as HTMLElement).dataset.modalOverlay && disabled !== true) {
        onClose();
      }
    },
    [disabled, onClose],
  );

  const handleButtonClick = useCallback(
    (type: 'primary' | 'secondary') => (event: React.MouseEvent<HTMLButtonElement>) => {
      onButtonClick?.(event, type);
    },
    [onButtonClick],
  );

  return hidden
    ? null
    : ReactDOM.createPortal(
        <StyledOverlay className='arthur-modal-overlay' data-modal-overlay onClick={onOverlayClick} ref={forwardedRef}>
          <StyledRoot className={classes} {...rest}>
            <div className='arthur-modal-inner'>
              <Icon className='arthur-modal-close' disabled={disabled} onClick={onClose} value='close' />
              <ModalHeading>{header}</ModalHeading>
              <ModalContent>{children}</ModalContent>
              <ModalFooter>
                {secondaryButtonText ? (
                  <Button disabled={disabled} onClick={handleButtonClick('secondary')} variant='secondary'>
                    {secondaryButtonText}
                  </Button>
                ) : null}
                {primaryButtonText ? (
                  <Button disabled={disabled} onClick={handleButtonClick('primary')} variant='default'>
                    {primaryButtonText}
                  </Button>
                ) : null}
              </ModalFooter>
            </div>
          </StyledRoot>
        </StyledOverlay>,
        document.body,
      );
});

export default Modal;
Modal.displayName = 'Modal';
